<template>
  <div id='main' ref='main'></div>
</template>

<script>
  import {getData} from '../../api/index/index.js'
  import {map} from '../../../public/data/city.js'
  export default{

  mounted(){
      getData().then(res => {
        console.log(res.result);
        this.mapinit(res.result);
        //this.getData1(res.result);
      })
      this.timer = setInterval(() => {
        getData().then(res => {
          this.mapinit(res.result);
        })
      },10000)
  },
  destroyed(){
    clearInterval(this.timer);
  },
  data(){
    return{
      map: {},
      timer: 0
    }
  },
  methods: {
    getData1(data){//根据城市名查找经纬度
        data.city.forEach(item => {
          for(let key in map.data){
            if(key.indexOf(item.name) != -1){
              item.value = map.data[key];
            }
          }
        })

        data.where.forEach(item => {
          for(let key in map.data){
            if(item.toName == key){
              item.coords[1] = map.data[key];
            }
            if(item.fromName == key){
              item.coords[0] = map.data[key];
            }
          }
        })
      this.map = data;
    },
    mapinit(res1){//攻击地图初始化
          let myChart = this.$echarts.init(this.$refs.main);
          let legend = [];
          let color = [];
          let obj = {};
          function randomHexColor() { //随机生成十六进制颜色
           var hex = Math.floor(Math.random() * 16777216).toString(16); //生成ffffff以内16进制数
           while (hex.length < 6) { //while循环判断hex位数，少于6位前面加0凑够6位
            hex = '0' + hex;
           }
           return '#' + hex; //返回‘#'开头16进制颜色
          }
          if(!res1.where) res1.where = [];
          res1.where.forEach((item,index) => {
            if(!obj[item.type]){
              color.push({
                type: item.type,
                color: randomHexColor()
              })
            }
          })


          function getcolor(type){//获取颜色
            let color1 = '';
            color.forEach(item => {
              if(item.type == type){
                color1 = item.color;
              }
            })
            return color1;
          }
           let option = {
               backgroundColor: 'transparent',
               legend: {
                orient: 'horizontal',
                bottom:'8%',
                right:'center',
                 data: legend,
                 textStyle: {
                     color: '#fff'
                 },
                 selectedMode: 'multiple',
               },
               geo: {
                   map: 'china',
                   aspectScale: 0.85,
                   layoutCenter: ["50%", "50%"], //地图位置
                   layoutSize: '100%',
                   itemStyle: {
                       normal: {
                           shadowColor: '#276fce',
                           shadowOffsetX: 0,
                           shadowOffsetY: 15,
                           opacity: 0.5,
                       },
                       emphasis: {
                           areaColor: '#276fce',
                       }
                   },
                   regions: [{
                       name: '南海诸岛',
                       itemStyle: {
                           areaColor: 'rgba(0, 10, 52, 1)',
                           borderColor: 'rgba(0, 10, 52, 1)',
                           normal: {
                               opacity: 0,
                               label: {
                                   show: false,
                                   color: "#009cc9",
                               }
                           },
                       },
                       label: {
                           show: false,
                           color: '#FFFFFF',
                           fontSize: 12,
                       },
                   }],

               },
               series: [
                   // 常规地图
                   {
                       type: 'map',
                       mapType: 'china',
                       aspectScale: 0.85,
                       layoutCenter: ["50%", "50%"], //地图位置
                       layoutSize: '100%',
                       zoom: 1, //当前视角的缩放比例
                       itemStyle: {
                           normal: {
                               areaColor: '#0c274b',
                               borderColor: '#1cccff',
                               borderWidth: 1.5
                           },
                           emphasis: {
                               areaColor: '#02102b',
                               label: {
                                   color: "#fff"
                               }
                           }
                       },


                   },
                   // 区域散点图
                   {
                       type: 'effectScatter',
                       coordinateSystem: 'geo',
                       zlevel: 2,
                       symbolSize: 10,
                       rippleEffect: { //坐标点动画
                           period: 3,
                           scale: 5,
                           brushType: 'fill'
                       },
                       label: {
                           normal: {
                               show: true,
                               position: 'right',
                               formatter: '{b}',
                               color: '#b3e2f2',
                               fontWeight: "200",
                               fontSize: 15
                           }
                       },
                       data: res1.city,
                       itemStyle: { //坐标点颜色
                           normal: {
                               show: true,
                               color: 'green',
                               shadowBlur: 15,
                               shadowColor: '#fff'
                           },
                           emphasis: {
                               areaColor: '#f00'
                           }
                       },
                       symbolSize: 5

                   },
                   // 线 和 点

               ]
           };

           let arr = [];
           let curveness = 0.3;
           let coords = [];
           let curcurveness = curveness;
           if(!res1.where) res1.where = [];
           res1.where.forEach((item,index) => {
             curcurveness = curveness;
             coords.forEach((items,indexs) => {
               if(items.toName == item.toName && items.fromName == item.fromName){
                  items.curveness+=0.1;
                  curcurveness = items.curveness;
               }
             })
             coords.push({
               fromName: item.fromName,
               toName: item.toName,
               curveness: curcurveness
             })
             legend.push(item.type);
             arr.push({
                 name: item.type,
                 type: 'lines',
                 zlevel: 2,
                 effect: {
                     show: true,
                     period: 4, //箭头指向速度，值越小速度越快
                     trailLength: 0.2, //特效尾迹长度[0,1]值越大，尾迹越长重
                     symbol: 'arrow', //箭头图标
                     symbolSize: 7, //图标大小
                 },
                 lineStyle: {
                     normal: {
                         color: getcolor(item.type),
                         width: 1, //线条宽度
                         opacity: 0.1, //尾迹线条透明度
                         curveness:curcurveness //尾迹线条曲直度
                     }
                 },
                 data: [{
                   fromName: item.fromName,
                   toName: item.toName,
                   coords: item.coords
               }]
             })
           })


          option.series = option.series.concat(arr);
          myChart.setOption(option,true);

    }
  }
}
</script>

