<template>
  <div class="editor" ref='editor' style='width: 380px;height: 260px;'></div>
</template>

<script>
  import {getWeekTrend} from '../../api/index/situation.js';
  export default{
    mounted(){
      getWeekTrend().then(res => {
        //console.log(res);
        this.init(res.result);
      })
    },
    methods: {
      init(res){
        let myChart = this.$echarts.init(this.$refs.editor);
        const colorList = ["#46b4e9", '#5aae72', '#302faf', '#F56948', '#9E87FF']
        const xData = res.md;
        let option = {
            backgroundColor: 'transparent',
            title: {
                text: '数量',
                textStyle: {
                    fontSize: 12,
                    fontWeight: 400,
                    color: '#69c5ee'
                },
                left: '10px',
                top: '5%'
            },
            legend: {
                orient: 'horizontal',
                top: '5%',
                right: '0',
                itemWidth: 6,
                itemGap: 20,
                textStyle: {
                    color: '#556677'
                },
                selectedMode: 'multiple',
            },
            tooltip: {
                trigger: 'axis',
                axisPointer: {
                    label: {
                        show: false,
                        backgroundColor: '#fff',
                        color: '#556677',
                        borderColor: 'rgba(0,0,0,0)',
                        shadowColor: 'rgba(0,0,0,0)',
                        shadowOffsetY: 0
                    },
                    lineStyle: {
                        width: 0
                    }
                },
                backgroundColor: '#fff',
                textStyle: {
                    color: '#5c6c7c'
                },
                padding: [10, 10],
                extraCssText: 'box-shadow: 1px 0 2px 0 rgba(163,163,163,0.5)',

            },
            grid: {
                top: '15%'
            },
            xAxis: [{
                type: 'category',
                data: xData,
                axisLine: {
                    lineStyle: {
                        color: 'rgba(107,107,107,0.37)', //x轴颜色
                    }
                },
                axisTick: {
                    show: true
                },
                axisLabel: {
                    interval: 0,
                    textStyle: {
                        color: '#69c5ee' //坐标轴字颜色
                    },
                    margin: 15
                },
                axisPointer: {
                    label: {
                        padding: [11, 5, 7],
                        backgroundColor: {
                            type: 'linear',
                            x: 0,
                            y: 0,
                            x2: 0,
                            y2: 1,
                            colorStops: [{
                                offset: 0,
                                color: '#fff' // 0% 处的颜色
                            }, {
                                offset: 0.9,
                                color: '#fff' // 0% 处的颜色
                            }, {
                                offset: 0.9,
                                color: '#33c0cd' // 0% 处的颜色
                            }, {
                                offset: 1,
                                color: '#33c0cd' // 100% 处的颜色
                            }],
                            global: false // 缺省为 false
                        }
                    }
                },
                boundaryGap: true
            }],
            yAxis: [{
                type: 'value',
               splitNumber: 5,
                axisTick: {
                    show: false
                },
                axisLine: {
                    show: false,
                    lineStyle: {
                        color: '#59a2cb', //y轴颜色
                    }
                },
                axisLabel: {
                  formatter: '{value}',
                    textStyle: {
                        color: '#69c5ee'
                    }
                },
                splitLine: {
                    lineStyle: {
                        type: 'dashed',
                        color: '#59a2cb'
                    }
                }
            }],
            series: [{
                name: '病毒',
                type: 'line',
                data: res.virus,
                symbolSize: 1,
                symbol: 'circle',
                smooth: true,
                yAxisIndex: 0,
                showSymbol: false,
                lineStyle: {
                    width: 2,
                    color: new this.$echarts.graphic.LinearGradient(0, 1, 0, 0, [{
                            offset: 0,
                            color: '#46b4e9'
                        },
                        {
                            offset: 1,
                            color: '#46b4e9'
                        }
                    ]),
                    shadowColor: 'rgba(158,135,255, 0.3)',
                    shadowBlur: 0
                },
                itemStyle: {
                    normal: {
                        color: colorList[0],
                        borderColor: colorList[0]
                    }
                }
            }, {
                name: '攻击',
                type: 'line',
                data: res.attack,
                symbolSize: 1,
                symbol: 'circle',
                smooth: true,
                yAxisIndex: 0,
                showSymbol: false,
                lineStyle: {
                    width: 2,
                    color: new this.$echarts.graphic.LinearGradient(1, 1, 0, 0, [{
                            offset: 0,
                            color: '#5aae72'
                        },
                        {
                            offset: 1,
                            color: '#5aae72'
                        }
                    ]),
                    shadowColor: 'rgba(115,221,255, 0.3)',
                    shadowBlur: 0,
                    shadowOffsetY: 0
                },
                itemStyle: {
                    normal: {
                        color: colorList[1],
                        borderColor: colorList[1]
                    }
                }
            }, {
                name: '漏洞',
                type: 'line',
                data: res.loophole,
                symbolSize: 1,
                symbol: 'circle',
                smooth: true,
                yAxisIndex: 0,
                showSymbol: false,
                lineStyle: {
                    width: 2,
                    color: new this.$echarts.graphic.LinearGradient(1, 1, 0, 0, [{
                            offset: 0,
                            color: '#302faf'
                        },
                        {
                            offset: 1,
                            color: '#302faf'
                        }
                    ]),
                    shadowColor: 'rgba(115,221,255, 0.3)',
                    shadowBlur: 0,
                    shadowOffsetY: 0
                },
                itemStyle: {
                    normal: {
                        color: colorList[2],
                        borderColor: colorList[2]
                    }
                }
            }]
        };

              myChart.setOption(option);
      }
    }
  }
</script>

<style>
</style>
